import { useEffect, useRef} from "react"

const Letter = ({letter, id, duration, firstWord}) => {
  const opacity = firstWord ? 'opacity-100' : 'opacity-0'
  return(
    <span id={id}
    className={`text-techTextActive transition duration-${duration} ease-in ${opacity} hover:opacity-0 cursor-pointer`}>
      {letter}
    </span>
  )
}

const Word = ({word, wordArr, durationFast, durationSlow, fastRegex, addFadeInStep, firstWord}) => {

  const wordId = `${word}-${wordArr.indexOf(word)}`

  const letterData = (() => {
    let data = []

    for( let i = 0; i < word.length; i++){
      const letterDuration = fastRegex.test(word[i]) ? durationFast : durationSlow
      const letterId = `${wordId}-${word[i]}-${i}-${fastRegex.test(word[i]) ? 'fast' : 'slow'}`

      addFadeInStep(letterId)

      const newLetter = {
        letter:word[i],
        id:letterId,
        duration:letterDuration
      }
      data.push(newLetter)
    }
    return data
  })()

  return(
    <>
      <div id={wordId}
      className="inline-block py-1">
        {letterData.map(l => 
        <Letter 
        key={l.id}
        letter={l.letter} 
        id={l.id} 
        duration={l.duration}
        firstWord={firstWord}/>)}
      </div>
      <span>{` `}</span>
    </>
  )
}

const FirstWord = ({word, wordArr, durationFast, durationSlow, fastRegex, addFadeInStep}) => {
  const firstId = `${word}-slow`
  addFadeInStep(firstId)
  return(
    <>
      <div id={firstId}
      className={`inline-block text-techTextActive transition duration-${durationSlow} ease-in opacity-0`}>
        <Word
          word={word}
          wordArr={wordArr}
          durationFast={durationFast}
          durationSlow={durationSlow}
          fastRegex={fastRegex}
          addFadeInStep={addFadeInStep}
          firstWord={true}
        />
      </div>
      <span key={`${firstId}-space`}>{` `}</span>
    </>
  )
}

export const ComplexFadeIn = ({inputText, fast, durationFast, slow, durationSlow, fastRegex, setDelay}) => {

  const fadeInStep = useRef([])

  const addFadeInStep = (id) => {
    fadeInStep.current.push(id)
  }

  useEffect(() => {
    setTimeout(()=>{
      let runningDelay = 0
      fadeInStep.current.forEach(ref => {
        const snip = ref.slice(ref.length - 4)
        runningDelay = snip === 'fast' ? runningDelay + fast : runningDelay + slow

        setTimeout(()=>{
          try{
            document.getElementById(ref).classList.replace('opacity-0', 'opacity-100')
          } catch(err){
            //handles if you go to the about page before callbacks are made
          }
        }, runningDelay)
      })
      setDelay(runningDelay)
    }, slow)
  }, [fast, slow, setDelay])

  document.addEventListener('keydown',()=>{
    try{
      fadeInStep.current.forEach(ref => {
        document.getElementById(ref).classList.replace('opacity-0','opacity-100')
      })
    } catch {

    }
  })

  const wordArr = inputText.split(" ")
  const firstWord = wordArr.shift()
  return (
    <>
      <FirstWord 
      word={firstWord}
      wordArr={wordArr}
      durationFast={durationFast}
      durationSlow={durationSlow}
      fastRegex={fastRegex}
      addFadeInStep={addFadeInStep}
      />
      {wordArr.map(word => 
        <Word
        key={`${word}-${wordArr.indexOf(word)}`}
        word={word}
        wordArr={wordArr}
        durationFast={durationFast}
        durationSlow={durationSlow}
        fastRegex={fastRegex}
        addFadeInStep={addFadeInStep}
        />)}
    </>
  )
}