import { Link } from "react-router-dom"
import { useState, useEffect } from "react"
import { Github } from '../icons/Github'
import { Linkedin } from '../icons/Linkedin'
import { Gmail } from '../icons/Gmail'

export const Header = ({page}) => {

  const start = 'text-2xl lg:text-4xl p-6'
  const scroll = 'lg:text-lg p-2 shadow-md'
  const currentPage = 'text-white'
  const regularColor = 'text-TechBlueLow'
  const startSize = 45
  const scrollSize = 30
  
  const [headerSize, setHeaderSize] = useState(start)
  const [iconSize, setIconSize] = useState(startSize)
  const [homeColor, setHomeColor] = useState(regularColor)
  const [aboutColor, setAboutColor] = useState(regularColor)

  useEffect(()=>{
    if(page === "home"){
      setHomeColor(currentPage)
    } else {
      setAboutColor(currentPage)
    }
    window.scrollTo(0,0)
  },[page])

  const handleScroll = () => {
    if(window.pageYOffset > 75){
      setHeaderSize(scroll)
      setIconSize(scrollSize)
    } else {
      setHeaderSize(start)
      setIconSize(startSize)
    }
  }

  window.addEventListener('scroll', handleScroll)

  return(
    <header className={`fixed z-20 ${headerSize} bg-techGray w-screen flex flex-wrap justify-center sm:justify-between md:pb-3 pt-2 font-poppins`}>
      <div className="py-2">
        <nav>
          <div className={`${homeColor} inline`}><Link className="cursor-pointer sm:px-5 mx-2 border-b-2 hover:text-techTextActive" to='/'>Home</Link></div>
          <div className={`${aboutColor} inline`}><Link className="cursor-pointer sm:px-5 mx-2 border-b-2 hover:text-techTextActive" to='/about'>About</Link></div>
        </nav>
      </div>
      <div className="sm:py-2">
        <ul className="flex flex-nowrap p-2 sm:px-10 sm:p-0">
          <li className="inline sm:mx-2">
            <a className="px-2"
              href="https://github.com/conorjamesward"
              target="_blank" rel="noreferrer">
              <Github size={iconSize}/>
            </a>
          </li>
          <li className="inline sm:mx-2">
            <a className="px-2"
              href="https://www.linkedin.com/in/conor-ward-01000011-01110111/"
              target="_blank" rel="noreferrer">
              <Linkedin size={iconSize} colorLess={true} className="inline"/>
            </a>
          </li>
          <li className="inline sm:mx-2">
            <a className="px-4"
              href="mailto:sirconorjamesward@gmail.com"
              target="_blank" rel="noreferrer">
              <Gmail size={iconSize} colorLess={true} className="inline"/>
            </a>
          </li>
        </ul>
      </div>
    </header>
  )
}