import hiking from './icons8-hiking-64.png'
import lift from './icons8-ski-lift-64.png'
import mountain from './icons8-mountain-64.png'
import climbing from './icons8-climbing-60.png'

export const Adventure = ({icon}) => {
  if (icon === 'hiking'){
    return <div className="inline-block"><img src={hiking} alt="hiking person"/></div>
  } else if (icon === 'lift'){
    return <div className="inline-block"><img src={lift} alt="ski lift"/></div>
  } else if (icon==='climbing') {
    return <div className="inline-block"><img src={climbing} alt="rock climber"/></div>
  } else return <div className="inline-block"><img src={mountain} alt="Mountain"/></div>
} 