import { ParticlesBackground } from './../components/ParticlesBackground'
import { Header } from '../components/Header'
import { Footer } from '../components/Footer'

import { Profile } from './homeComponents/Profile'
import { Contact } from './homeComponents/Contact'
import { TechSkills } from './homeComponents/TechSkills'
import { Projects } from './homeComponents/Projects'
import { HomeIntro } from './homeComponents/HomeIntro'
import { HomeAftroduction } from './homeComponents/HomeAftroduction'

import { useState, useEffect, useRef, useCallback } from 'react'

export const Home = () => {

  const fadeInComponents = useRef(["header", "skillsAndProjects", "contact", "footer"])

  const extraDelay = 500
  const duration = 500

  const [totalDelay, setTotalDelay] = useState(0)
  const [load, setLoad] = useState(false)

  
  const setDelay = useCallback((delay) => {
    setTotalDelay(delay + extraDelay)
  },[])

  //todo: make this a promise?

  window.addEventListener('load', () => {
    setLoad(true)
  })

  useEffect(()=>{
    setTimeout(()=>{
      if (load){
        setTimeout(()=>{
          try{
            fadeInComponents.current.forEach(component => {
              document.getElementById(component).classList.replace('opacity-0', 'opacity-100')
            })
          } catch(err){
            //handles if you go to another page before callback is executed
          }
        },totalDelay)
      } else {
        fadeInComponents.current.forEach(component => {
          document.getElementById(component).classList.replace('opacity-0', 'opacity-100')
        })
      }
    },extraDelay / 2)
  },[load, totalDelay])

  document.addEventListener('keydown',() => {
    try{
      fadeInComponents.current.forEach(component => {
        document.getElementById(component).classList.replace('opacity-0','opacity-100')
      })
    } catch{

    }
  })

  return(
    <>
      <div id="header" className={`transition duration-${duration} ease-in opacity-0`}>
        <Header page="home"/>
      </div>
      <div className="absolute flex-col z-10 w-full font-poppins">
        <HomeIntro setDelay={setDelay}/>
        <HomeAftroduction/>
        <div id="skillsAndProjects" className={`transition duration-${duration} ease-in opacity-0 font-semibold`}>
          <TechSkills/>
          <Projects/>
        </div>
        <div id="contact" className={`mx-auto m-10 transition duration-${duration} ease-in opacity-0 font-poppins`}>
          <div className="bg-techText opacity-80 grid gap-4 grid-cols-1 lg:grid-cols-2">
            <div className="font-semibold">
              <Profile/>
            </div>
            <div>
              <Contact title={'Contact Me'} border={'border-2'}/>
            </div>
          </div>
        </div>
        <div id="footer" className={`transition duration-${duration} ease-in opacity-0`}>
          <Footer/>
        </div>
      </div>
      <ParticlesBackground/>
    </>
  )
}