import { useState } from 'react'
import { Gmail } from '../icons/Gmail'

export const ContactInfo = ({title, size, border = ''}) => {
  const copiableValue = 'conorjameswarddeveloper@gmail.com'

  const [copyMessageVisible, setCopyMessageVisible] = useState('invisible')

  const handleCopy = async () => {
    await navigator.clipboard.writeText(copiableValue)
    setCopyMessageVisible('visible')
    setTimeout(()=>{
      setCopyMessageVisible('invisible')
    }, 3000)
  }
  
  return (
    <div>
      <h4 className="text-center text-xl border-b-2 p-2">{title}</h4>
      <h5 className={`${copyMessageVisible} text-confirmationGreen bg-confirmationBackground border-2 text-center p-1 m-1 mx-5 lg:mx-10 xl:mx-20`}><strong>Email copied to cliboard</strong></h5>
      <div className={`flex justify-center flex-wrap p-2 ${border}`}>
        <div className="inline-block pt-4">
          <a className="hover:text-techTextActive" href="mailto:conorjameswarddeveloper@gmail.com?subject=I saw your github pages site" target="_blank" rel="noreferrer">
            <div className="px-4">
              <Gmail colorLess={true} size={size}/>
            </div>
          </a>
        </div>
        <div className="inline-block mt-2 sm:text-lg">
          <p className="hover:text-techTextActive cursor-pointer flex flex-wrap" onClick={handleCopy}>{copiableValue}</p>
          <p><a href="tel:1-502-550-7793" className="hover:text-techTextActive">(502) 550-7793</a></p>
        </div>
      </div>
    </div>
  )
}