import { useState, useEffect } from 'react'
import { Education } from './introductionComponents/Education'
import { ContactMe } from './introductionComponents/ContactMe'
import { General } from './introductionComponents/General'


export const Introduction = () => {

  const [showIndex, setShowIndex] = useState(0)
  const [segments, setSegments] = useState(null)

  useEffect(()=>{
    setSegments([<General/>, <ContactMe/>, <Education/>])
    document.getElementById('education').click()
  },[])

  const handleIntroSegment = (e) => {

    document.getElementById('education').classList.replace('text-white', 'text-techBlueLow')
    document.getElementById('contactMe').classList.replace('text-white', 'text-techBlueLow')
    document.getElementById('general').classList.replace('text-white', 'text-techBlueLow')
    
    if(e.target.id === 'education'){
      e.target.classList.replace('text-techBlueLow', 'text-white')
      setShowIndex(0)
    } else if (e.target.id === 'contactMe'){
      e.target.classList.replace('text-techBlueLow', 'text-white')
      setShowIndex(1)
    } else if (e.target.id === 'general'){
      e.target.classList.replace('text-techBlueLow', 'text-white')
      setShowIndex(2)
    }
  }

  return(
    <div className="flex justify-center bg-techText pb-60 pt-20 ">
      <div className="sectionContainer">
        <ul className="flex-1 justify-center flex-wrap">
          <li className="sectionLiElement">
            <div id="education" onClick={handleIntroSegment}
            className="text-white sectionTitle hover:text-techTextActive">General</div>
          </li>
          <li className="sectionLiElement">
            <div id="contactMe" onClick={handleIntroSegment}
            className="text-white sectionTitle hover:text-techTextActive">Contact Me</div>
          </li>
          <li className="sectionLiElement">
            <div id="general" onClick={handleIntroSegment}
            className="text-white sectionTitle hover:text-techTextActive">Professional History</div>
          </li>
        </ul>
        <div className="p-5">
          {segments && segments[showIndex]}
        </div>
      </div>
    </div>   
  )
}