import { Summary } from './Summary'
import { Details } from './Details'
import { Dot } from '../../../../icons/Dot'
import { useState } from 'react'

export const General = () => {

  const size = 9
  const less = 'Less'
  const more = 'More'

  const [moreOrLess, setMoreOrLess] = useState(more)

  const handleMoreOrLess = () => {
    if(moreOrLess === less) {
      setMoreOrLess(more)
    } else {
      setMoreOrLess(less)
    }
  }

  return(
    <div className="text-techBlueLow">
      <div className="p-4">
        <h4 className="p-4 text-center text-2xl border-b-2 font-semibold">Who I Am</h4>
      </div>
      <div>
        <ul className="text-center md:flex justify-center text-xl font-semibold">
          <li className="md:inline-block">Polymath</li>
          <li className="inline-block p-3">
            <Dot size={size}/>
          </li>
          <li className="md:inline-block">Motivated</li>
          <li className="inline-block p-3">
            <Dot size={size}/>
          </li>
          <li className="md:inline-block">Largely Self-Taught</li>
        </ul>
      </div>
      <div>
        <Summary/>
        {moreOrLess === less ? <Details/> : null}
        <div className="flex justify-center pt-4 pb-0">
          <button className="font-semibold text-xl py-2 px-10 border-2 hover:text-techTextActive" onClick={handleMoreOrLess}>{`${moreOrLess}`}</button>
        </div>
      </div>
    </div>
  )
}