import { useState, useEffect } from 'react'
import {Project} from './Project'
import {Dot} from '../../icons/Dot'
import { Contact } from './Contact'

export const Projects = () => {

  const projects = [
    {
      id:0,
      name:'Super Simple Task Tracker',
      decription:'Simple Chrome Extension',
      icon:'icon',
      url:null,
      tech:['HTML','CSS','JavaScript','Chrome Extension APIs','basic security'],
      images:null,
    },
    {
      id:1,
      name:'2',
      decription:'testing',
      icon:null,
      url:null,
      tech:['test', 'testing', 'test 123'],
      images:null,
    },
    {
      id:2,
      name:'3',
      decription:'test',
      icon:null,
      url:null,
      tech:['test'],
      images:null,
    },
  ]

  const [current, setCurrent] = useState(0)
  const [past, setPast] = useState(null)

  const [startTouch, setStartTouch] = useState(null)

  const handleCurrent = (newCurrent) => {
    setPast(current)
    if(newCurrent < 0){
      setCurrent(projects.length - 1)
    } else if(newCurrent > projects.length - 1){
      setCurrent(0)
    }else {
      setCurrent(newCurrent)
    }
  }

  //TODO:destructuring
  const handleTouch = (e) => {
    console.log(e)
    if(e._reactname === 'onTouchStart'){
      setStartTouch(e.changedTouches.screenX)
    } else {
      const screenX = e.changedTouches.screenX
      console.log(screenX)
      console.log(startTouch)
      if(e.changedTouches.clientX / Math.abs(screenX - startTouch) > 0.1){
        if(screenX < startTouch){
          handleCurrent(current - 1)
        } else {
          handleCurrent(current + 1)
        }
      }
      setStartTouch(null)
    }
  }

  useEffect(()=>{
    const newDot = document.getElementById(`${current}-dot`)
    newDot.classList.replace('scale-100', 'scale-150')
    newDot.classList.replace('-translate-y-0', '-translate-y-2')
    if(past !== null){
      const oldDot = document.getElementById(`${past}-dot`)
      oldDot.classList.replace('scale-150','scale-100')
      oldDot.classList.replace('-translate-y-2', '-translate-y-0')
    }
  },[current, past])

  return(
    <div>
      <h2 className="text-center text-techTextActive text-2xl pt-20 pb-4">Projects I've made</h2>
      <div className="text-techTextActive border-t-2 mb-32">
        <div className="flex justify-between">
          <button id="previous" onClick={()=>handleCurrent(current - 1)}
          className="invisible md:visible text-8xl transition duration-300 transform hover:-translate-x-2 mx-3 z-30 mt-1/2">
            {`<`}
          </button>
          <div id="container" className="w-full" onTouchStart={handleTouch} onTouchEnd={handleTouch}>
            <Project project={projects[current]}/>
          </div>
          <button id="next" onClick={()=>handleCurrent(current + 1)}
          className="invisible md:visible text-8xl transition duration-300 transform hover:translate-x-2 mx-3 z-30">
            {'>'}
          </button>
        </div>
        <div className="flex justify-center">
          {projects.map(project => 
          <button id={`${project.id}-dot`} key={project.id} onClick={()=>{handleCurrent(project.id)}} className="inline-block p-2 m-2 cursor-pointer transition duration-300 scale-100 -translate-y-0 transform hover:scale-150 hover:-translate-y-2">
            <Dot fill='#D2D8DB' size={9}/>
          </button>)}
        </div>
      </div>
    </div>
  )
}