import { useState, useEffect} from "react"
export const useFadeInOutOnView = (targetId) => {

  const handleFade = (entires) => {
    if (entires[0].isIntersecting) {
      target.classList.replace('animate-fadeOut', 'animate-fadeIn')
    } else {
      target.classList.replace('animate-fadeIn', 'animate-fadeOut')
    }
  }

  //TODO: useref
  
  const [target, setTarget] = useState(null)

  useEffect(() => {
    setTarget(document.getElementById(targetId))
  },[targetId])

  if(target){
    
    const options = {
      root: null,
      rootMargin:"0px",
      threshold:0.2,
    }
  
    const observer = new IntersectionObserver(handleFade, options)
  
    observer.observe(target)
  }
}